export default {
  name: 'BigSwiper',
  props: ['carouselList', 'height', 'msg'],
  methods: {
    // 轮播图切换修改背景色
    swiperChange(e) {
      const index = e.detail.current;
      this.swiperCurrent = index;
    },
    swiper_navToDetailPage(item) {
      this.$emit('swiper_navToDetailPage', { id: item.id});
    },
  },
};
